<template>
  <div>
    <div class="all-box box">
      <div class="box-title">
        <div class="blue-icon"></div>
        <span>业务分布</span>
      </div>
      <com-kip :kipData="kipData" class="com-kip"></com-kip>
      <app-echarts :options="mapOption" style="height: 240px" :theme="theme" />

      <div class="mapLegend">
        <div class="item" v-for="(item,index) in mapLegend" :key="index">
          <div class="icon" :style="mapLegend[index].color"></div>
          <span>{{item.label}}</span>
        </div>
      </div>
    </div>
    <div class="all-box box">
      <div class="box-title">
        <div class="blue-icon"></div>
        <span>指标完成</span>
      </div>
      <com-tabButton @buttonClick="buttonClick" :buttonArr="buttonArr"></com-tabButton>
      <app-echarts :options="lineOption" style="height: 230px" :theme="theme" />
      <!-- <div class="kip-box">
        <span class="name">计划完成</span>
        <span class="value">{{newPlanamt}}</span>
        <span class="unit">亿美元</span>
      </div>-->
      <app-echarts :options="two_pieOptions" style="height: 230px" :theme="theme" />
      <com-iconList :iconListArr="iconListArr"></com-iconList>
    </div>
  </div>
</template>

<script>
import commonService from "../common";
export default {
  name: "allState",
  data() {
    return {
      newPlanamt: 0,
      completeLegend: {
        "0": "工程承包",
        "1": "勘察设计咨询",
        "2": "房地产",
        "3": "投资服务",
        "4": "装备制造",
        "5": "物资物流",
        "6": "金融服务及新兴产业"
      },
      theme: "echart-theme",
      mapLegend: [
        {
          label: "中国",
          value: 0,
          color: "background-color:#e81c07"
        },
        {
          label: "有机构有项目有人员",
          value: 1,
          color: "background-color:#6da5f9"
        },
        {
          label: "有机构无项目有人员",
          value: 2,
          color: "background-color:#e69c59"
        },
        {
          label: "有机构无项目无人员",
          value: 3,
          color: "background-color:#faea59"
        },
        {
          label: "无机构有项目有人员",
          value: 4,
          color: "background-color:#5df1a4"
          // color: "#bebebe"
        },
        {
          label: "未准入国别",
          value: 5,
          color: "background-color:#dfdfdf"
          // color: "#bebebe"
        }
      ],
      one_pieOptions: {},
      two_pieOptions: {},
      barOptions: {},
      buttonArr: [{ name: "新签合同额" }, { name: "完成营业额" }],
      buttonModel: 0,
      iconListArr: [],
      mapOption: {},
      kipData: [
        { name: "在建项目", value: 0, unit: "个" },
        { name: "总合同额", value: 0, unit: "亿美元" },
        { name: "在外中方人员", value: 0, unit: "人" }
      ],
      // kipData2: [{ name: "计划完成", value: 191.918, unit: "亿美元" }],
      mapData: []
    };
  },
  mounted() {
    this.completeData();
    // 地图
    // const mapData = [
    //   {
    //     projectNumber: 0,
    //     stateCode: "196",
    //     stateName: "汤加",
    //     colorClass: "4"
    //   },
    //   {
    //     personNumber: 2,
    //     projectNumber: 0,
    //     stateCode: "95",
    //     stateName: "俄罗斯",
    //     colorClass: "4"
    //   },
    //   { projectNumber: 0, stateCode: "05", stateName: "日本", colorClass: "4" },
    //   {
    //     projectNumber: 1,
    //     stateCode: "01",
    //     stateName: "中国",
    //     colorClass: "3",
    //     projectMoney: 0.02,
    //     personNumber: 2
    //   }
    // ];
    this.mapOption = this.ChartInitService.middleMap(this.mapData);
    this.$axios
      .get(`/api/Server/hw/homepage/v1.0/mapdata/00001`, {
        headers: {
          Authorization: "Bearer " + this.$store.state.api_token,
          "Content-Type": "application/json"
        }
      })
      .then(result => {
        this.mapData = result.data.data.mapData;
        this.mapData.forEach(item => {
          if (item.projectMoney !== 0) {
          }
        });
        this.kipData = [
          {
            name: "在建项目",
            value: _.sumBy(this.mapData, "projectNumber"),
            unit: "个"
          },
          {
            name: "总合同额",
            value: _.sumBy(this.mapData, "projectMoney").toFixed(2),
            unit: "亿美元"
          },
          {
            name: "在外中方人员",
            value: _.sumBy(this.mapData, "personNumber"),
            unit: "人"
          }
        ];
        this.mapData.forEach(item => {
          item.name = this.ChartInitService.nameMapC[item.stateName];
          if (item.stateName === "中国") {
            item.value = 0;
          } else {
            item.value = item.colorClass;
          }
          // else if (item.name && item.projectNumber && item.personNumber) {
          //   item.value = 1;
          // } else if (item.name && item.personNumber) {
          //   item.value = 2;
          // } else if (item.name) {
          //   item.value = 3;
          // } else {
          //   item.value = 4;
          // }
        });
        this.mapOption = this.ChartInitService.middleMap(this.mapData);
        this.mapOption.tooltip = {
          formatter: params => {
            return `${this.ChartInitService.nameMapE[params.name]}<br/>
              在建项目个数(个)：
              ${
                params.data.projectNumber ? +params.data.projectNumber : 0
              } <br/>
              合同额(亿美元)：
              ${params.data.projectMoney ? params.data.projectMoney : 0} <br/>
              在外中方人员(个)： 
                ${params.data.personNumber ? +params.data.personNumber : 0}`;
          }
        };
        this.mapOption.visualMap.pieces = [
          {
            label: "中国",
            value: 0,
            color: "#e81c07"
          },
          {
            label: "有机构有项目有人员",
            value: 1,
            color: "#6da5f9"
          },
          {
            label: "有机构无项目有人员",
            value: 2,
            color: "#e69c59"
          },
          {
            label: "有机构无项目无人员",
            value: 3,
            color: "#faea59"
          },
          {
            label: "无机构有项目有人员",
            value: 4,
            color: "#5df1a4"
            // color: "#bebebe"
          },
          {
            label: "未准入国别",
            value: 5,
            // color: "#5df1a4"
            color: "#bebebe"
          }
        ];
      });
  },
  methods: {
    completeData() {
      // const data = {
      //   completeData: [
      //     { plateSort: "0", completeMoney: 0.15, completeNumber: 7 },
      //     {
      //       plateSort: "1",
      //       completeMoney: 0.14,
      //       completeMoneyRmb: 13736.0,
      //       completeNumber: 6
      //     },
      //     {
      //       plateSort: "2",
      //       completeMoney: 0.0,
      //       completeMoneyRmb: 8.0,
      //       completeNumber: 2
      //     },
      //     {
      //       plateSort: "3",
      //       completeMoney: 0.0,
      //       completeMoneyRmb: 0.0,
      //       completeNumber: 0
      //     },
      //     {
      //       plateSort: "4",
      //       completeMoney: 0.0,
      //       completeMoneyRmb: 0.0,
      //       completeNumber: 0
      //     },
      //     {
      //       plateSort: "5",
      //       completeMoney: 0.0,
      //       completeMoneyRmb: 0.0,
      //       completeNumber: 0
      //     },
      //     {
      //       plateSort: "6",
      //       completeMoney: 0.0,
      //       completeMoneyRmb: 0.0,
      //       completeNumber: 0
      //     }
      //   ]
      // };
    },
    // 世界热力地图
    // https://gallery.echartsjs.com/editor.html?c=xBkrC4fjs-

    buttonClick(name, i) {
      this.buttonModel = i;
      let legend = [];
      let seriesData = [];
      let legends = [];
      let seriesDatas = [];
      let api = "";
      if (i === 0) {
        api = `/api/Server/hw/homepage/v1.0/yearPlan/10000227/00001`;
      } else if (i === 1) {
        // 完成营业额
        api = `/api/Server/hw/homepage/v1.0/completion/00001`;
      }

      Promise.all([
        this.$axios.get(
          `/api/Server/hw/homepage/v1.0/yearPlan/10000227/00001`,
          {
            headers: {
              Authorization: "Bearer " + this.$store.state.api_token,
              "Content-Type": "application/json"
            }
          }
        ),
        this.$axios.get(`/api/Server/hw/homepage/v1.0/completion/00001`, {
          headers: {
            Authorization: "Bearer " + this.$store.state.api_token,
            "Content-Type": "application/json"
          }
        })
      ]).then(values => {
        let result = [];
        if (i === 0) {
          this.newPlanamt = JSON.parse(values[0].data.data).newPlanamt
            .newLabelUS
            ? JSON.parse(values[0].data.data).newPlanamt.newLabelUS
            : 0;
          result = values[0];
        } else {
          this.newPlanamt = JSON.parse(values[0].data.data).newPlanamt
            .finishMoneyUS
            ? JSON.parse(values[0].data.data).newPlanamt.finishMoneyUS
            : 0;
          result = values[1];
        }
        let completeData = [];
        let valuePeiod = "";
        let centerValue = [];
        let seriesDatas = [];
        let legends = [];
        for (let key in this.completeLegend) {
          legends.push(this.completeLegend[key]);
        }
        let xData = ["年度计划", "本年合计"];
        let valueData1 = [[], [], [], [], [], [], []];
        if (i === 0) {
          completeData = JSON.parse(result.data.data).performList;
          completeData.forEach((item, index) => {
            valueData1[index].push(0, (item.yearperform / 10000).toFixed(3));
          });
          valuePeiod = "yearperform";
          centerValue = [
            {
              value: (_.sumBy(completeData, "yearperform") / 10000).toFixed(3),
              name: "完成营业额"
            },
            { name: "计划", value: 0 }
          ];
          seriesDatas = completeData.map(item => {
            return {
              name: this.completeLegend[item["type"]],
              value: (item[valuePeiod] / 10000).toFixed(2)
            };
          });
        } else if (i === 1) {
          completeData = result.data.data;
          completeData.forEach((item, index) => {
            valueData1[index].push(0, item.completeMoney);
          });
          valuePeiod = "completeMoney";
          centerValue = [
            {
              value: _.sumBy(completeData, "completeMoney").toFixed(3),
              name: "完成营业额"
            },
            // 疑问
            { name: "计划", value: 0 }
          ];
          seriesDatas = completeData.map(item => {
            return {
              name: this.completeLegend[item.plateSort],
              value: item[valuePeiod]
            };
          });
        }

        let value = 0;
        centerValue.forEach(item => {
          if (item.name !== "计划") {
            value = item.value;
          }
        });

        this.lineOption = this.ChartInitService.multipleBarLine_1(
          legends,
          valueData1,
          xData,
          { xoy: "y", stack: true }
        );
        this.lineOption["legend"]["show"] = false;
        this.lineOption["tooltip"] = {
          trigger: "axis",
          axisPointer: { type: "shadow" },
          formatter: params => {
            let txt = "";
            params.forEach(item => {
              if (item.name === "年度计划") {
                txt = item.data;
              } else if (item.data !== 0) {
                txt += `${item.seriesName}:  ${item.data}亿美元</br>`;
              }
            });
            return txt;
          }
        };
        // this.one_pieOptions = this.ChartInitService.basicPie(
        //   legend,
        //   centerValue
        // );
        // this.one_pieOptions.title = [
        //   {
        //     text: "亿美元",
        //     x: "center",
        //     top: "58%",
        //     textStyle: {
        //       color: "#000000",
        //       fontSize: 16,
        //       fontWeight: "400"
        //     }
        //   },
        //   {
        //     text: value,
        //     x: "center",
        //     top: "42%",
        //     textStyle: {
        //       fontSize: "26",
        //       color: "#0088fe",
        //       fontFamily: "Lato",
        //       fontWeight: "600"
        //     }
        //   }
        // ];
        // this.one_pieOptions.tooltip = {
        //   formatter: params => {
        //     if (params.name === "计划") {
        //       return "";
        //     } else {
        //       return params.data.name + "：" + params.data.value + "亿美元";
        //     }
        //   }
        // };

        // this.one_pieOptions.color = ["#0088fe", "#f3f2f7"];
        this.two_pieOptions = this.ChartInitService.basicPie(
          legends,
          seriesDatas,
          {
            labelShow: true
          }
        );

        this.two_pieOptions.title = [
          {
            text: this.buttonArr[this.buttonModel].name + "透视",
            x: "center",
            top: "48%",
            textStyle: {
              color: "#000000",
              fontSize: 17,
              fontWeight: "400"
            }
          }
        ];
        this.iconListArr = seriesDatas;
        this.iconListArr.forEach(item => {
          item.midValue = commonService.GetPercent(
            item.value,
            this.iconListArr
          );
        });
      });

      // this.$axios
      //   .get(`${api}`, {
      //     headers: {
      //       Authorization: "Bearer " + this.$store.state.api_token,
      //       "Content-Type": "application/json"
      //     }
      //   })
      //   .then(result => {});
    }
  }
};
</script>

<style scoped lang="less">
p.title {
  margin-top: 3.8vh;
  margin-bottom: 0vh;
}
.kip-box {
  text-align: center;
  .value {
    color: #0088fe;
  }
}
.com-kip {
  /deep/.kip-item {
    width: 33% !important;
    margin-bottom: 0 !important;
  }
}
</style>


