<template>
  <div>
    <div class="all-box box">
      <div class="box-title">
        <div class="blue-icon"></div>
        <span>重大重点</span>
      </div>
      <com-kip :kipData="kipData"></com-kip>
      <com-table :thead="thead1" :tbody="tbody1" :iconShow="true"></com-table>
      <!-- <com-iconList :iconListArr="iconListArr" :midUnitShow="true"></com-iconList> -->
    </div>
    <div class="all-box box" v-for="(item,index) in listTotalArr" :key="index">
      <div class="box-title">
        <div class="blue-icon"></div>
        <span>{{listObj[item.projectStatus]}}</span>
      </div>
      <com-kip :kipData="item.arr"></com-kip>
    </div>
    <div class="all-box box">
      <div class="box-title">
        <div class="blue-icon"></div>
        <span>项目类别</span>
      </div>
      <com-tabButton @buttonClick="buttonClick" :buttonArr="buttonArr" class="tab-button"></com-tabButton>
      <div style="height: 400px; overflow: auto">
        <app-echarts :options="lineOption" style="height: 800px" :theme="theme" />
      </div>
      <div style="height: 480px; overflow: auto;margin-top: 2vh;">
        <com-table :thead="thead2" :tbody="tbody2"></com-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "statis",
  data() {
    return {
      lineObj: {
        "004": "水利建设项目",
        "008": "电力工程建设",
        "010": "通讯工程建设",
        "011": "其他",
        "006": "交通运输建设项目",
        "002": "房屋建筑项目",
        "001": "制造加工设施建设项目"
      },
      listTotalArr: [],
      listObj: {
        "1": "正在实施",
        "6": "签约待开工",
        "5": "完工",
        "2": "待融资",
        "7": "终止",
        "4": "完工未验交",
        "3": "停缓建"
      },
      theme: "echart-theme",
      buttonArr: [{ name: "单位" }, { name: "专业" }],
      kipData: [
        { name: "重点重大项目数", value: 0, unit: "个" },
        { name: "在总项目数占比", value: 0, unit: "%" },
        { name: "重点重大项目金额", value: 0, unit: "亿美元" },
        { name: "在总项目金额占比", value: 0, unit: "%" }
      ],
      kipData1: [
        { name: "完工项目", value: 68, unit: "个" },
        { name: "环比", value: 4, unit: "个" },
        { name: "总合同额", value: 267, unit: "亿美元" },
        { name: "环比", value: 76, unit: "个" }
      ],
      thead1: [
        { name: "级别" },
        { name: "项目数", unit: "个" },
        { name: "合同额", unit: "亿美元" }
      ],
      tbody1: [
        {
          name: "重点",
          middle: 0,
          end: 0
        },
        {
          name: "重大",
          middle: 0,
          end: 0
        }
      ],
      thead2: [
        { name: "企业" },
        { name: "项目数", unit: "个" },
        { name: "合同额", unit: "亿美元" }
      ],
      tbody2: [
        { name: "中铁一局", middle: 20, end: 29.808 },
        { name: "中铁二局", middle: 20, end: 22.863 },
        { name: "中铁三局", middle: 20, end: 39.066 },
        { name: "中铁四局", middle: 20, end: 1.987 },
        { name: "中铁五局", middle: 20, end: 8.665 },
        { name: "中铁六局", middle: 20, end: 10.982 }
      ],
      iconListArr: [
        {
          name: "重点",
          midValue: 45,
          value: 342
        },
        {
          name: "重大",
          midValue: 16,
          value: 164
        }
      ],
      lineOption: {}
    };
  },
  mounted() {
    this.topKip();
    this.middleKip();
  },
  methods: {
    topKip() {
      this.$axios
        .get(`/api/Server/hw/projectimplement/v1.0/homepage/great/00001`, {
          headers: {
            Authorization: "Bearer " + this.$store.state.api_token,
            "Content-Type": "application/json"
          }
        })
        .then(result => {
          this.kipData = [
            {
              name: "重点重大项目数",
              value: result.data.data.filter(item => item.isGreat === "小计")[0]
                .subtotalNumber,
              unit: "个"
            },
            { name: "在总项目数占比", value: 20, unit: "%" },
            {
              name: "重点重大项目金额",
              value: result.data.data.filter(item => item.isGreat === "小计")[0]
                .subtotalMoney,
              unit: "亿美元"
            },
            { name: "在总项目金额占比", value: 52, unit: "%" }
          ];
          this.tbody1 = [
            {
              name: "重点",
              middle: result.data.data.filter(
                item => item.isGreat === "重点关注项目"
              )[0].greatNumber,
              end: result.data.data.filter(
                item => item.isGreat === "重点关注项目"
              )[0].greatMoney
            },
            {
              name: "重大",
              middle: result.data.data.filter(
                item => item.isGreat === "重大项目"
              )[0].greatNumber,
              end: result.data.data.filter(
                item => item.isGreat === "重大项目"
              )[0].greatMoney
            }
          ];
        });
    },
    middleKip() {
      this.$axios
        .get(
          `/api/Server/hw/projectimplement/v1.0/homepage/projectstatus/00001/a1ce189af0d511eaa854f9568e4563e7`,
          {
            headers: {
              Authorization: "Bearer " + this.$store.state.api_token,
              "Content-Type": "application/json"
            }
          }
        )
        .then(result => {
          this.listTotalArr = result.data.data;
          this.listTotalArr.forEach(item => {
            item.arr = [
              {
                name: this.listObj[item.projectStatus] + "项目",
                value: item.projectNumber
              },
              { name: "环比", value: item.lastMonthNumber },
              { name: "总合同额", value: item.projectMoney },
              { name: "环比", value: item.lastMoney }
            ];
          });
        });
    },

    buttonClick(name, i) {
      let api = "";
      if (i === 0) {
        api = `Server/hw/projectimplement/v1.0/homepage/specialty?orgCode=00001&operation=unit&projectClass=&projectStatus=`;
      } else {
        api =
          "Server/hw/projectimplement/v1.0/homepage/specialty?orgCode=00001&operation=specialty&projectClass=&projectStatus=";
      }
      this.$axios
        .get(`/api/${api}`, {
          headers: {
            Authorization: "Bearer " + this.$store.state.api_token,
            "Content-Type": "application/json"
          }
        })
        .then(result => {
          const legend = ["数量(个)", "金额(亿美元)"];
          const xData = [];
          const valueData = [[], []];
          result.data.data.forEach(element => {
            xData.unshift(
              i === 0 ? element.orgName : this.lineObj[element.orgName]
            );
            valueData[0].unshift(element.projectCount);
            valueData[1].unshift(element.contractMoney);
          });
          this.lineOption = this.ChartInitService.multipleBarLine_1(
            legend,
            valueData,
            xData,
            { xoy: "y" }
          );
          this.lineOption["yAxis"][0]["axisLabel"] = {
            formatter: params => {
              return params.length > 6
                ? params.substring(0, params.length / 2) +
                    "\n" +
                    params.substring(params.length / 2, params.length)
                : params;
            }
          };
          this.lineOption["grid"]["top"] = "8%";

          this.tbody2 = [];
          result.data.data.forEach(element => {
            this.tbody2.push({
              name: i === 0 ? element.orgName : this.lineObj[element.orgName],
              middle: element.projectCount,
              end: element.contractMoney
            });
          });
        });
    }
  }
};
</script>

<style>
</style>


