<template>
  <div>
    <van-pull-refresh v-model="pedding.refreshing" @refresh="onRefresh1">
      <van-list
        v-model="pedding.loading"
        :finished="pedding.finished"
        finished-text
        @load="penddingLoad"
      >
        <com-txtList :listArr="pedding.list"></com-txtList>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
export default {
  name: "remindPage",
  data() {
    return {
      pedding: {
        loading: false,
        finished: false,
        refreshing: false,
        page: 1,
        list: []
      },
      index: 0
    };
  },
  mounted() {},
  methods: {
    onRefresh1() {
      // 清空列表数据
      this.pedding = {
        loading: false,
        finished: false,
        refreshing: false,
        page: 1,
        list: []
      };
      this.penddingLoad();
    },
    listClick(id) {},
    penddingLoad() {
      this.getPenddingData();
    },
    getPenddingData() {
      this.pedding.loading = true;
      this.$axios
        .post(
          `/api/Server/hw/info/v1.0`,
          {
            current: this.pedding.page,
            size: 5,
            sort: {},
            condition: {}
          },
          {
            headers: {
              Authorization: "Bearer " + this.$store.state.api_token,
              "Content-Type": "application/json"
            }
          }
        )
        .then(result => {
          this.pedding.loading = false;
          this.pedding.list = result.data.data.records.map(item => {
            return {
              auditingName: item.content,
              nodeCreateTime: item.createTime,
              icon: "项目"
            };
          });
          // this.pedding.list.push(...result.data.data.records);
          this.pedding.page1++;
          if (this.pedding.list.length === result.data.data.total) {
            this.pedding.finished = true;
          }
        });
    }
  }
};
</script>

<style lang="less" scoped>
.tab-button {
  margin-top: 2vh;
  .button-item {
    // width: 40px;
    text-align: center;
  }
}
</style>



