<template>
  <div>
    <com-tabButton class="tabButton" @buttonClick="buttonClick" :buttonArr="buttonArr"></com-tabButton>
    <distribute v-if="buttonModel === 0"></distribute>
    <statis v-if="buttonModel === 1"></statis>
    <productValue v-if="buttonModel === 2"></productValue>
  </div>
</template>

<script>
import distribute from "@/views/analysis/project/distribute.vue";
import statis from "@/views/analysis/project/statis.vue";
import productValue from "@/views/analysis/project/product-value.vue";
export default {
  name: "project",
  components: {
    distribute,
    statis,
    productValue
  },
  data() {
    return {
      buttonArr: [],
      buttonModel: 0
    };
  },
  mounted() {
    this.buttonArr = [
      { name: "业务分布" },
      { name: "项目统计" },
      { name: "项目产值" }
    ];
  },
  methods: {
    buttonClick(name, i) {
      this.buttonModel = i;
    }
  }
};
</script>

<style>
.tabButton {
  margin-top: 2vh;
}
</style>


