<template>
  <div>
    <van-tabs v-model="active">
      <van-tab title="工作任务">
        <page-work></page-work>
      </van-tab>
      <van-tab title="会议安排">
        <page-arrange></page-arrange>
      </van-tab>
      <van-tab title="出差计划">
        <page-out></page-out>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import pageArrange from "@/views/information/calendar/add-meeting/arrange.vue";
import pageWork from "@/views/information/calendar/add-meeting/work.vue";
import pageOut from "@/views/information/calendar/add-meeting/out.vue";
export default {
  components: {
    pageArrange,
    pageWork,
    pageOut
  },
  data() {
    return {
      active: 1
    };
  }
};
</script>

<style  scoped lang="less">
</style>


