<template>
  <ul class="list-box">
    <li class="list all-box" v-for="(item,i) in listArr" :key="i" @click="listClick(item.id)">
      <div class="title-box">
        <div class="title-png">{{item.icon ? item.icon : '假数据'}}</div>
        <!-- <div class="blue-circle"></div> -->
        <span class="title">{{item.auditingName}}</span>
      </div>
      <p class="date">{{item.nodeCreateTime}}</p>
    </li>
  </ul>
</template>

<script>
export default {
  name: "com-txtList",
  props: {
    listArr: {
      default: []
    }
  },
  data() {
    return {};
  },
  mounted() {},
  methods: {
    listClick(id) {
      this.$emit("listClick", id);
    }
  }
};
</script>

<style lang="less" >
.list-box {
  min-height: auto;
  padding-top: 0.8vh;
  .list.all-box {
    border-bottom: 1px solid #ccc;
    overflow: hidden;
    padding-bottom: 10px;
    margin-top: 1vh;
    min-height: auto;
    .title-box {
      border-bottom: 1px solid #d6d6d6;
      padding-bottom: 2.8vh;
      // display: flex;
      align-items: center;
      margin-top: 1vh;
      .blue-circle {
        // float: left;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #0088fe;
        margin-right: 2%;
      }
      .title-png {
        color: #fff;
        float: left;
        background-color: #f58a22;
        font-size: 15px !important;
        padding: 2px 8px;
        border-radius: 4px;
        margin-right: 6px;
      }
      .title {
        text-overflow: ellipsis;
        overflow: hidden;
        display: block;
        white-space: nowrap;
      }
    }
    p {
      margin-top: 1.8vh;
    }

    .title,
    .title-png {
      font-size: 17px;
    }
    .date {
      color: #a8a8a8;
      float: right;
    }
  }
}
</style>



