<template>
  <div class="wd-calendar">
    <div class="month-container">
      <div v-if="monthSelectable" @click="changeMonth(-1)" class="btn forback">
        <van-icon name="arrow-left" />
      </div>
      <div
        class="date-container"
        @change="confirm()"
        :readonly="!monthSelectable"
      >
        {{ currentMonth }}
      </div>
      <div v-if="monthSelectable" @click="changeMonth(1)" class="btn forward">
        <van-icon name="arrow" />
      </div>
    </div>
    <div class="weekday-container">
      <span
        class="weekday-text"
        :key="k"
        :aria-label="`周${week}`"
        v-for="(week, k) in ['一', '二', '三', '四', '五', '六', '日']"
        >{{ week }}</span
      >
    </div>
    <div class="calendar-list-container">
      <div
        v-for="(month, index) in monthArray"
        :key="index"
        class="calendar-row"
      >
        <span
          v-for="(item, itemIndex) in month"
          :key="`${index}-${itemIndex}`"
          class="row-item"
          :class="item.itemClassArray"
          :accessible="true"
          :aria-label="`${item.text ? item.text : ''}`"
          @click="changeDay(item)"
        >
          <span
            class="item-day"
            :class="item.textClassArray"
            :style="item.textStyle"
            >{{ item.text }}</span
          >
          <div
            class="item-desc"
            :class="item.descClassArray"
            :style="item.descStyle"
          >
          <span class="status-point" style=" background-color:#67C23A" v-show="isShowDesc(item.date)"/>
          </div>
        </span>
      </div>
    </div>
  </div>
</template>
<script>
import map from 'lodash/map'
import includes from 'lodash/includes'
import { format, isValid, addMonths, differenceInDays, parse } from 'date-fns'
import {
  // GetMonthTitle,
  GetMonthArray
} from './util'

const monthFormat = 'YYYY-MM'
const dayFormat = 'DD'
export default {
  name: 'ZcttCalendar',
  props: {
    initMonth: {
      type: String,
      validator(value) {
        return isValid(new Date(parse(value)))
      },
      default: format(new Date(), monthFormat),
      description: '初始月份'
    },
    initDay: {
      type: String,
      default: format(new Date(), dayFormat),
      description: '初始日期'
    },
    daySelectable: {
      type: Boolean,
      default: true,
      description: '是否可选择天'
    },
    monthSelectable: {
      type: Boolean,
      default: true,
      description: '是否可选择月'
    },
    descList: {
      type: Array,
      default: () => [],
      description: '数组集合，格式为[{date:"2018-12-17",text:"",style:{}}]'
    },
    cancelSelect: {
      type: Boolean,
      default: true,
      description: '是否可以取消选中'
    }
  },
  data() {
    return {
      currentMonth: this.initMonth,
      selectedDate: null
    }
  },
  computed: {
    monthArray() {
      return GetMonthArray(this.currentMonth, this.selectedDate, this.descList)
    },
    descMap(){
      return map(this.descList , 'date')
    }
  },
  created() {
    if (this.initDay) {
      const defaultDate = new Date(`${this.initMonth}-${this.initDay}`)
      if (isValid(defaultDate)) {
        this.changeDay({
          date: defaultDate
        })
      }
    }
  },
  methods: {
    changeDay(datConfig) {
      /**
       * @description 日期切换事件
       *
       */
      const self = this
      if (!this.daySelectable || datConfig.disabled) {
        return
      }
      const isUnselect =
        this.selectedDate &&
        differenceInDays(datConfig.date, this.selectedDate) === 0
      if (isUnselect && !this.cancelSelect) {
        return
      }
      this.selectedDate = isUnselect ? null : datConfig.date
      self.$emit('dayChange', isUnselect ? null : datConfig)
    },
    changeMonth(value) {
      /**
       * @description 月份切换，返回当前月份 ；日期切换
       *
       */
      if (!this.monthSelectable) {
        return
      }
      this.currentMonth = value
        ? format(
            addMonths(new Date(parse(this.currentMonth)), value),
            monthFormat
          )
        : format(new Date(parse(this.currentMonth)), monthFormat)
      this.selectedDate = null
      this.$emit('month-change', this.currentMonth)
      // this.$emit('dayChange', null)
    },
    isShowDesc(date) {
      date = format(date , 'YYYY-MM-DD')
      return includes(this.descMap , date)
    }
  }
}
</script>
<style lang="less" scoped>
@month-height: 30px;
.status-point{
  display: inline-block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
}
.wd-calendar {
  color: #333;
  background-color: #fff;
  padding: 10px;
  .month-container {
    height: @month-height;
    line-height: @month-height;
    display: flex;
    justify-content: space-between;
    .date-container {
      line-height: @month-height;
      float: left;
      margin: 0 20px;
    }
    .btn {
      float: left;
      width: 50px;
      height: 30px;
      position: relative;
      top: 0;
      text-align: center;
      color: #0079fe;
      line-height: 30px;
      cursor: pointer;

      &.forward {
        right: 0;
      }
      &.forback {
        left: 0;
      }
    }
  }
  .weekday-container {
    display: flex;
    height: 25px;
    background-color: #fff;
    border-bottom-width: 1px;
    border-top-width: 1px;
    border-color: #e2e2e2;
    align-items: center;
    margin-top: 10px;
  }
  .weekday-text {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    -webkit-flex: 1;
    flex: 1;

    display: block;
    text-align: center;
    color: #000;
    font-size: 15px;
  }

  .calendar-row {
    display: flex;
  }
  .row-item {
    display: block;
    height: 35px;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    text-align: center;
    cursor: pointer;
    position: relative;
    padding-top: 5px;
  }
  .item-day {
    width: 24px;
    height: 35px;
    font-size: 15px;
    text-align: center;
  }
  .item-desc {
    font-size: 15px;
    text-align: center;
    position: relative;
    top: -5px;
  }
}
</style>
<style lang="less">
.wd-calendar {
  .row-item-selected {
    background-color: #c2e5f3;
  }
  .row-item-text-disabled {
    color: #a8a7a7;
  }
}
</style>



