<template>
  <div id="app">
    <van-nav-bar
      :title="navTitle"
      :left-arrow="this.$router.currentRoute.path !=='/information'"
      @click-left="onClickLeft"
    />
    <router-view />
    <van-tabbar v-model="active" @change="tabChange">
      <van-tabbar-item icon="comment-o">
        <!-- <router-link :to="{ path: '/homepage' }"></router-link> -->
        消息
      </van-tabbar-item>
      <!-- <van-tabbar-item icon="search">分析</van-tabbar-item> -->
      <van-tabbar-item icon="chart-trending-o">概况</van-tabbar-item>
      <van-tabbar-item icon="coupon-o">工作台</van-tabbar-item>
      <van-tabbar-item icon="friends-o">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Toast } from "vant";
import findIndex from "lodash/findIndex";
import includes from "lodash/includes";
export default {
  name: "App",
  data() {
    return {
      tabbarArr: [
        {
          name: "消息",
          path: "/information",
          children: [{ name: "会议", path: "/information/addmeeting" }]
        },
        // { name: "总体分析", path: "/analysis" },
        { name: "总体概况", path: "/analysis" },
        { name: "海外业务信息服务", path: "/work" },
        { name: "我的", path: "/my" }
      ],
      active: 0,
      navTitle: ""
    };
  },
  watch: {
    $route: {
      handler(newValue) {
        // this.active = this.tabbarArr.indexOf(newValue.path);
        if (includes(newValue.path, "/information")) {
          this.navTitle = this.tabbarArr[0].name;
          this.active = 0;
        } else if (includes(newValue.path, "/analysis")) {
          this.navTitle = this.tabbarArr[1].name;
          this.active = 1;
        } else if (includes(newValue.path, "/work")) {
          this.navTitle = this.tabbarArr[2].name;
          this.active = 2;
        } else if (includes(newValue.path, "/my")) {
          this.navTitle = this.tabbarArr[3].name;
          this.active = 3;
        } else {
          this.navTitle = "";
        }
      }
    }
  },
  mounted() {
    if (includes(this.$router.currentRoute.path, "/information")) {
      this.navTitle = this.tabbarArr[0].name;
      this.active = 0;
    } else if (includes(this.$router.currentRoute.path, "/analysis")) {
      this.navTitle = this.tabbarArr[1].name;
      this.active = 1;
    } else if (includes(this.$router.currentRoute.path, "/work")) {
      this.navTitle = this.tabbarArr[2].name;
      this.active = 2;
    } else if (includes(this.$router.currentRoute.path, "/my")) {
      this.navTitle = this.tabbarArr[3].name;
      this.active = 3;
    } else {
      this.navTitle = "";
    }
  },
  methods: {
    tabChange(key) {
      this.$router.push(this.tabbarArr[key].path);

      this.navTitle = this.tabbarArr[key].name;
      // if (key === 0) {
      //   this.$router.push("/information");
      // } else if (key === 1) {
      //   this.$router.push("/analysis");
      // }
    },
    // 疑问
    onClickLeft() {
      this.$router.currentRoute.path !== "/information"
        ? window.history.go(-1)
        : "";
    }
  }
};
</script>

<style lang="less" type="text/less">
p {
  margin: 0;
}
body {
  background-color: #f3f2f7;
}
.label {
  color: #787e85;
}
</style>
<style  scoped lang="less" type="text/less">
#app {
  min-height: 85vh;
  // font-family: "Avenir", Helvetica, Arial, sans-serif;
  font-family: "微软雅黑";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  background-color: #f3f2f7;
  color: #000000;
  font-size: 14px;
  padding-bottom: 50px;
}
// 通用样式
/deep/.box {
  .name {
    font-size: 16px;
    line-height: 1.8;
  }
  .value {
    font-weight: bold;
    font-size: 20px;
  }
  .unit {
    font-size: 14px;
    color: #787e85;
  }
  .kip-box {
    overflow: hidden;
    width: 106%;
    margin-left: -4%;

    .kip-item {
      position: relative;
      float: left;
      width: 50%;
      text-align: center;
      margin-bottom: 6px;
      margin-top: 10px;
      &:nth-last-child(1),
      &:nth-last-child(2) {
        margin-bottom: 0;
      }
      .value {
        color: #0088fe;
        font-size: 22px;
      }
      .arrow {
        position: absolute;
        right: 22%;
        bottom: 20%;
      }
    }
  }
  .icon-box {
    display: flex;
    align-items: center;
    width: 42%;
    .icon {
      width: 20px;
      height: 10px;
      background-color: red;
      float: left;
      margin-right: 8px;
    }
  }
  div.middle {
    width: 25%;
  }
  div.right {
    width: 33%;
    text-align: right;
  }
  .icon-list {
    margin: 3vh 0;
    font-size: 16px;
    display: flex;
    // justify-content: space-between;
    align-items: center;

    .percent {
    }
  }
  p.title {
    text-align: center;
    font-size: 16px;
  }
  .box-title {
    margin-bottom: 2vh;
    padding-bottom: 1.4vh;
    border-bottom: 1px solid #d6d6d6;
    font-size: 16px;
    // &::after {
    //   width: 10px;
    //   height: 10px;
    //   background-color: blue;
    // }
    .blue-icon {
      float: left;
      margin-right: 8px;
      width: 4px;
      margin-top: 0.2vh;
      height: 2.8vh;
      border-radius: 2vh;
      background-color: #0088fe;
    }
  }
  .tab-button {
    display: flex;
    justify-content: space-around;
    .button-item {
      min-width: 50px !important;
      padding: 1vh 4.5vw;
      background-color: #ffffff;
      border: 1px solid #0088fe;
      color: #0088fe;
      border-radius: 4px;
    }
    .button-item-active {
      padding: 1vh 4.5vw;
      background-color: #0088fe;
      color: #fff;
      border-radius: 4px;
    }
  }
  .mapLegend {
    width: 108%;
    margin-left: -2%;
    // display: flex;
    margin-top: -12%;
    overflow: hidden;
    .item {
      display: flex;
      align-items: center;
      margin-bottom: 1.8vh;
      width: 50%;
      float: left;
      &:nth-child(1) {
        width: 100%;
      }
      .icon {
        width: 13px;
        height: 13px;
        background-color: red;
        margin-right: 8px;
      }
      span {
        font-size: 16px;
      }
    }
  }
}

/deep/.all-box {
  background-color: #fff;
  margin: 2vh 1.6vh;
  padding: 3% 5%;
  border-radius: 10px;
  min-height: 18vh;
}
/deep/.echarts {
  width: 100% !important;
}

/deep/.van-nav-bar {
  height: 35px;
  padding-top: 10px;
  .van-nav-bar__arrow {
    font-size: 20px !important;
  }
}
.van-tabs__line {
  background-color: #1989fa;
}
/deep/.van-tabs__wrap {
  border-bottom: 1px solid #ebedf0;
  .van-tab {
    margin: 2vh 0;
    border-right: 1px solid #f3f4f4;
    font-size: 15px;
  }
  .van-tabs__line {
    width: 50px;
  }
}
/deep/.van-tabs--line .van-tabs__wrap {
  height: 6.8vh;
}

.van-tab span {
  color: #787e85;
}
</style>



