<template>
  <div>
    <div class="all-box box">
      <div class="box-title">
        <div class="blue-icon"></div>
        <span>产值计划</span>
      </div>
      <com-tabButton @buttonClick="buttonClick1" :buttonArr="buttonArr1" class="tab-button"></com-tabButton>
      <app-echarts :options="pieOption" style="height: 230px" :theme="theme" />
      <com-kip :kipData="kipData" class="com-kip"></com-kip>
    </div>
    <div class="all-box box">
      <div class="box-title">
        <div class="blue-icon"></div>
        <span>产值分析</span>
      </div>
      <com-tabButton @buttonClick="buttonClick2" :buttonArr="buttonArr2" class="tab-button"></com-tabButton>

      <div style="height: 400px; overflow: auto">
        <app-echarts :options="lineOption" style="height: 800px" :theme="theme" />
      </div>
      <div style="height: 480px; overflow: auto;margin-top: 2vh;">
        <com-table :thead="thead" :tbody="tbody"></com-table>
      </div>
      <!-- <app-echarts :options="lineOption" style="height: 370px" :theme="theme" />
      <com-table :thead="thead" :tbody="tbody"></com-table>-->
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      theme: "echart-theme",
      buttonArr1: [{ name: "年产值" }, { name: "月产值" }],
      buttonArr2: [{ name: "总产值" }, { name: "完成情况" }],
      kipData: [
        { name: "总计划", value: 320, unit: "亿美元" },
        { name: "完成", value: 95, unit: "%" },
        { name: "环比", value: 14.3, unit: "%" }
      ],
      thead: [
        { name: "单位" },
        { name: "目标产值", unit: "亿美元" },
        { name: "已完成产值", unit: "亿美元" }
      ],
      tbody: [
        // { name: "房建", middle: 72, end: 39 },
        // { name: "隧道", middle: 37, end: 23 },
        // { name: "桥梁", middle: 42, end: 25 },
        // { name: "市政", middle: 62, end: 15 },
        // { name: "公路", middle: 46, end: 30 },
        // { name: "铁路", middle: 78, end: 20 }
      ],
      lineOption: {},
      pieOption: {}
    };
  },
  mounted() {},
  methods: {
    buttonClick1(name, i) {
      this.$axios
        .get(`/api/Server/hw/projectimplement/v1.0/homepage/production/00001`, {
          headers: {
            Authorization: "Bearer " + this.$store.state.api_token,
            "Content-Type": "application/json"
          }
        })
        .then(result => {});

      this.pieOption = {
        backgroundColor: "#fff",
        tooltip: {
          show: false
        },
        legend: {
          show: false,
          orient: "vertical",
          x: 690,
          y: 120,
          data: ["一般"]
        },
        series: [
          {
            name: "一般",
            type: "pie",
            //起始刻度的角度，默认为 90 度，即圆心的正上方。0 度为圆心的正右方。
            startAngle: 0,
            hoverAnimation: false,
            tooltip: {},
            radius: ["90%", "110%"],
            center: ["50%", "60%"],
            // label: {
            //   normal: {
            //     show: false,
            //     position: "center",
            //     color: "#fff",
            //     formatter: function(params) {
            //       return params.value;
            //     }
            //   }
            // },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: 320,
                itemStyle: {
                  normal: {
                    color: "rgba(80,150,224,0)"
                  }
                }
              },
              {
                value: 270.34,
                itemStyle: {
                  normal: {
                    color: "#11c05f"
                  }
                },
                label: {
                  normal: {
                    show: true,
                    formatter: "{c|{c}} \n {a|本年产值} \n {b|亿美元} \n {d|}",
                    rich: {
                      c: {
                        color: "#0088fe",
                        fontSize: 34,
                        fontWeight: "bold",
                        lineHeight: 50
                      },
                      b: {
                        color: "#787e85",
                        fontSize: 12,
                        lineHeight: 0
                      },
                      d: {
                        color: "#FBFE27",
                        fontSize: 16,
                        lineHeight: 50
                      },
                      a: {
                        align: "center",
                        color: "#000",
                        fontSize: 18,
                        height: 0
                      }
                    },
                    textStyle: {
                      fontSize: 20,
                      fontWeight: "bold"
                    },
                    position: "center"
                  }
                }
              },
              {
                value: 49.66,
                itemStyle: {
                  normal: {
                    color: "rgba(80,150,224,0.1)"
                  }
                }
              }
            ]
          }
        ]
      };
    },

    buttonClick2(name, i) {
      let api = "";
      let legend = [];
      if (i === 0) {
        api = `Server/hw/projectimplement/v1.0/homepage/accomplishPercent/00001/z`;
        legend = ["总产值(亿美元)", "目标产值(亿美元)"];
      } else {
        api =
          "Server/hw/projectimplement/v1.0/homepage/accomplishPercent/00001/b";
        legend = ["未完成(亿美元)", "已完成(亿美元)"];
      }
      this.$axios
        .get(`/api/${api}`, {
          headers: {
            Authorization: "Bearer " + this.$store.state.api_token,
            "Content-Type": "application/json"
          }
        })
        .then(result => {
          const xData = [];
          const valueData = [[], []];
          if (i === 0) {
            result.data.data.forEach(element => {
              xData.unshift(element.unit);
              valueData[0].unshift(element.productionSum);
              valueData[1].unshift(element.productionPlanSum);
            });
          } else {
            result.data.data.forEach(element => {
              xData.unshift(element.unit);
              valueData[0].unshift(element.unfinished);
              valueData[1].unshift(element.productionSum);
            });
          }

          this.lineOption = this.ChartInitService.multipleBarLine_1(
            legend,
            valueData,
            xData,
            { xoy: "y" }
          );
          this.lineOption["yAxis"][0]["axisLabel"] = {
            formatter: params => {
              return params.length > 6
                ? params.substring(0, params.length / 2) +
                    "\n" +
                    params.substring(params.length / 2, params.length)
                : params;
            }
          };
          this.lineOption["grid"]["top"] = "8%";

          this.tbody = [];
          result.data.data.forEach(element => {
            this.tbody.push({
              name: element.unit,
              middle: element.productionPlanSum,
              end: element.productionSum
            });
          });
        });
    }
  }
};
</script>

<style lang="less" scoped>
.com-kip {
  margin-top: -18%;
  /deep/.kip-item {
    width: 33% !important;
    margin-bottom: 0 !important;
  }
}
</style>


