<template>
  <div class="all-box box">
    <div class="box-title">
      <div class="blue-icon"></div>
      <span>业务分布</span>
    </div>
    <com-kip :kipData="kipData"></com-kip>
    <!-- <com-tabButton @buttonClick="buttonClick" :buttonArr="buttonArr" class="tab-button"></com-tabButton> -->
    <app-echarts :options="mapOption" style="height: 240px" :theme="theme" />

    <div class="mapLegend">
      <div class="item" v-for="(item,index) in mapLegend" :key="index">
        <div class="icon" :style="mapLegend[index].color"></div>
        <span>{{item.label}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import commonService from "../common";
export default {
  name: "distribute",
  data() {
    return {
      theme: "echart-theme",
      mapLegend: [
        {
          label: "<1千万美元",
          lt: 0.01,
          color: "background-color: #dfdfdf"
        },
        {
          label: "1千万美元~1亿美元",
          gt: 0.01,
          lt: 1,
          color: "background-color:#dfdfdf"
        },
        {
          label: "1亿美元~5亿美元",
          gt: 1,
          lt: 5,
          color: "background-color:#11c05f"
        },
        {
          label: "5亿美元~10亿美元",
          gt: 5,
          lt: 10,
          color: "background-color:#6da5f9"
        },
        {
          label: "10亿美元~20亿美元",
          gt: 10,
          lt: 20,
          color: "background-color:#faea59"
        },
        {
          label: ">20亿美元",
          gt: 20,
          color: "background-color:#e69c59"
          // color: "bebebe"
        }
      ],
      mapOption: {},
      // buttonArr: [{ name: "国家" }, { name: "大洲" }],
      kipData: [
        { name: "在施项目", value: 0, unit: "个" },
        { name: "总合同额", value: 0, unit: "亿美元" }
      ]
    };
  },
  mounted() {
    this.mapOption = this.ChartInitService.middleMap(this.mapData);
    this.$axios
      .get(`/api/Server/hw/projectimplement/v1.0/homepage/mapdata/00001`, {
        headers: {
          Authorization: "Bearer " + this.$store.state.api_token,
          "Content-Type": "application/json"
        }
      })
      .then(result => {
        this.mapData = result.data.data.mapData;
        this.kipData = [
          {
            name: "在施项目",
            value: _.sumBy(this.mapData, "underwayNumber"),
            unit: "个"
          },
          {
            name: "总合同额",
            value: _.sumBy(this.mapData, "underwayMoney").toFixed(2),
            unit: "亿美元"
          }
        ];
        this.mapDatas = this.mapData.map(item => {
          return { value: item.underwayMoney };
        });
        this.mapData.forEach(item => {
          item.name = this.ChartInitService.nameMapC[item.vcountryName];
          // const a = commonService.GetPercent(item.underwayMoney, this.mapDatas);
          item.value = item.underwayMoney;
        });
        this.mapOption = this.ChartInitService.middleMap(this.mapData);
        this.mapOption.tooltip = {
          formatter: params => {
            return `${this.ChartInitService.nameMapE[params.name]}<br/>
              在施项目个数(个)：
              ${
                params.data.underwayNumber ? +params.data.underwayNumber : 0
              } <br/>
              合同额(亿美元)：
              ${params.data.underwayMoney ? params.data.underwayMoney : 0}`;
          }
        };
        this.mapOption.visualMap.pieces = [
          {
            label: "<1000万美元",
            lt: 0.01,
            color: "#dfdfdf"
          },
          {
            label: "1000万美元~1亿美元",
            gt: 0.01,
            lt: 1,
            color: "#dfdfdf"
          },
          {
            label: "1亿美元~5亿美元",
            gt: 1,
            lt: 5,
            color: "#11c05f"
          },
          {
            label: "5亿美元~10亿美元",
            gt: 5,
            lt: 10,
            color: "#6da5f9"
          },
          {
            label: "10亿美元~20亿美元",
            gt: 10,
            lt: 20,
            color: "#faea59"
          },
          {
            label: ">20亿美元",
            gt: 20,
            color: "#e69c59"
            // color: "bebebe"
          }
        ];
      });
  },
  methods: {
    buttonClick(name, i) {}
  }
};
</script>

<style lang="less" scoped>
.mapLegend {
  .item {
    width: 50% !important;
  }
}
.tab-button {
  margin-top: 1.8vh;
}
</style>


