<template>
  <div class="kip-box">
    <div class="kip-item" v-for="(item,index) in kipData" :key="index">
      <div class="name">{{item.name}}</div>
      <span class="value">{{item.value}}</span>
      <div class="unit">{{item.unit}}</div>
      <div class="arrow" v-if="item.name.indexOf('环比') > -1">
        <img src="../assets/image/arrow.png" alt />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "com-kip",
  props: {
    kipData: {
      default: []
    }
  },
  data() {
    return {};
  },
  mounted() {}
};
</script>

<style lang="less" >

</style>



