<template>
  <div class="index-list">
    <div class="all-box">
      <van-cell title="发起会议时间" :value="date" @click="show = true" />
      <van-calendar v-model="show" @confirm="onConfirm" />
    </div>
    <div
      class="all-box list-box"
      v-for="(item,index) in listArr"
      :key="index"
      @click="addClick(item)"
    >
      <p class="title">{{item.jobContent}}</p>
      <div class="list">
        <div class="item">
          <div class="label">发起人：</div>
          <span>{{item.personName}}</span>
        </div>
        <div class="item">
          <div class="label">参会人：</div>

          <span
            v-for="(itemC,index) in JSON.parse(item.correlationPerson)"
            :key="index"
          >{{itemC ? itemC.name: '' }} {{index===JSON.parse(item.correlationPerson).length-1 ? '': '、'}}</span>
        </div>
        <div class="item">
          <div class="label">参会日期：</div>
          <span>{{item.meetingTime}}</span>
        </div>
      </div>
    </div>

    <div class="add-circle" @click="addClick('')">
      <van-icon name="plus" />
    </div>
  </div>
</template>

<script>
export default {
  name: "indexList",
  data() {
    return {
      date: "",
      show: false,
      listArr: [
        {
          title: "安哥拉铁路7局项目资质审核",
          startPerson: "张宇",
          addPerson: "石磊、王美娟",
          startDate: "09:10",
          endDate: "10：10"
        },
        {
          title: "安哥拉铁路7局项目资质审核",
          startPerson: "张宇",
          addPerson: "石磊、王美娟",
          startDate: "09:10",
          endDate: "10：10"
        },
        {
          title: "安哥拉铁路7局项目资质审核",
          startPerson: "张宇",
          addPerson: "石磊、王美娟",
          startDate: "09:10",
          endDate: "10：10"
        }
      ]
    };
  },
  mounted() {
    // 疑问
    console.log(window.config1);
    this.$axios
      .post(
        `/api/Server/hw/homepage/v1.0/calendar/list`,
        {
          condition: { calendarTime: "2020-11" }
        },
        {
          headers: {
            Authorization: "Bearer " + this.$store.state.api_token,
            "Content-Type": "application/json"
          }
        }
      )
      .then(result => {
        console.log(result.data.data);
        this.listArr =
          result.data.data.length !== 0 ? result.data.data[0].hwHpCalendar : [];
      });
  },
  methods: {
    addClick(item) {
      this.$router.push({
        name: "Addmeeting",
        params: item
      });
      this.$store.commit("setInformation", 2);
    },
    formatDate(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
    },
    onConfirm(date) {
      this.show = false;
      this.date = this.formatDate(date);
    }
  }
};
</script>

<style lang="less" scoped>
.add-circle {
  position: fixed;
  z-index: 999;
  right: 18px;
  bottom: 80px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #0088fe;
  .van-icon {
    font-size: 35px;
    color: #fff;
    position: absolute;
    top: 24%;
    left: 24%;
  }
}
.all-box.list-box {
  min-height: auto;
  .title {
    font-size: 19px;
    margin-bottom: 2vh;
  }
  .list {
    .label {
      float: left;
      min-width: 80px;
      text-align: right;
    }
    .item {
      margin: 1.4vh 0;
      font-size: 16px;
      overflow: hidden;
    }
  }
}
</style>


