<template>
  <div class="calendar-demo-container">
    <zctt-calendar
      @dayChange="dayChange"
      @month-change="monthChange"
      :desc-list="list"
    />
  </div>
</template>

<script>
export default {
  name: "demo",
  data() {
    return {
      list: [
        { date: "2020-11-17" },
        { date: "2020-11-18" },
        { date: "2020-11-19" },
        { date: "2020-11-20" }
      ] // 小点的日期格式
    };
  },
  methods:{
    dayChange(day){
      console.log(`选择天触发的事件:`)
      console.log(day)
    },
    monthChange(month){
      console.log(`选择月触发的事件:`)
      console.log(month)
    }
  }
};
</script>



