import _ from 'lodash'
import {
  getDay,
  startOfMonth,
  getDaysInMonth,
  addMonths,
  addDays,
  differenceInDays,
  format
} from 'date-fns'

export function GetMonthArray(dateMonth, selectedDate, descList) {
  const daysArr = []
  // 周日的weekday是0不是7！！
  const preMonthDays = (7 + getDay(startOfMonth(new Date(dateMonth))) - 1) % 7
  const currentMonthDays = getDaysInMonth(new Date(dateMonth))
  const nextMonthDays =
    (7 - getDay(startOfMonth(addMonths(new Date(dateMonth), 1))) + 1) % 7
  const totalDays = preMonthDays + currentMonthDays + nextMonthDays

  const firstDate = addDays(new Date(dateMonth), -preMonthDays)

  for (let i = 0; i < totalDays; i++) {
    const firstIndex = Math.floor(i / 7)
    const secondIndex = i % 7
    if (secondIndex === 0) {
      daysArr[firstIndex] = []
    }
    const currentDay = addDays(firstDate, i)
    const disabled = i < preMonthDays || i > totalDays - nextMonthDays - 1
    const selected =
      !!selectedDate && differenceInDays(selectedDate, currentDay) === 0
    const desc = disabled
      ? null
      : _.find(descList, ['date', format(currentDay, 'YYYY-MM-DD')])
    daysArr[firstIndex][secondIndex] = _.merge(
      {
        text: format(currentDay, 'D'),
        list: desc ? desc.list : [],
        date: currentDay,
        disabled,
        selected,
        itemClassArray: [
          disabled ? 'row-item-disabled' : '',
          selected ? 'row-item-selected' : ''
        ],
        textClassArray: [disabled ? 'row-item-text-disabled' : '']
      },
      {
        desc: desc ? desc.text : '',
        descStyle: desc ? desc.style : {},
        descClassArray: [disabled ? 'row-item-desc-disabled' : '']
      }
    )
  }
  return daysArr
  
}

export function GetMonthTitle(dateMonth) {
  return format(new Date(dateMonth), 'YYYY-MM')
}



