<template>
  <div class="com-table">
    <table>
      <thead>
        <tr>
          <th v-for="(item,index) in thead" :key="index">
            <div>
              <div>{{item.name}}</div>
              <div
                v-show="item.unit"
                class="unit"
                style="line-height: 0;padding-bottom:2vh"
              >({{item.unit}})</div>
            </div>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in tbody" :key="index">
          <td>
            <div class="icon-box">
              <div class="icon" :style="listBackColor[index]" v-if="iconShow"></div>
              <span class="name">{{item.name}}</span>
            </div>
          </td>
          <td>
            <span class="value">{{item.middle}}</span>
          </td>
          <td>
            <span class="value">{{item.end}}</span>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "com-table",
  props: {
    thead: {
      default: []
    },
    tbody: {
      default: []
    },
    iconShow: {
      default: false
    }
  },
  data() {
    return {
      listBackColor: [
        "background-color:#6da5f9",
        "background-color:#e81c07",
        "background-color:#fc602d",
        "background-color:#f58a22",
        "background-color:#d066f3",
        "background-color:#11c05f",
        "background-color:#00a7fe"
      ]
    };
  },
  mounted() {}
};
</script>

<style lang="less" scope>
.com-table {
  width: 100%;
  margin-top: 16px;

  table {
    border: 0;
    width: 100%;
    border-collapse: collapse;
    font-size: 15px;
  }

  th {
    background-color: #f3f2f7;
    // color: #fff;
    width: 33.3%;
  }

  td,
  th {
    padding: 0;
    text-align: center;
    line-height: 6vh;
  }
  td {
    font-size: 16px !important;
    .icon-box {
      justify-content: center;
      width: 100% !important;
      .icon {
        width: 12px !important;
        height: 12px !important;
      }
    }
    .value {
      font-size: 17px !important;
      font-weight: normal !important;
    }
  }

  tr:nth-child(even) {
    background-color: #f8f8f8;
  }
}
</style>



