import _sumBy from 'lodash/sumBy'
var commonService = {
    // 求百分数
    GetPercent(num, arr) {
        let total = _sumBy(arr, "value");
        num = parseFloat(num);
        total = parseFloat(total);
        if (isNaN(num) || isNaN(total)) {
            return "-";
        }
        return total <= 0
            ? "0%"
            : (Math.round((num / total) * 10000) / 100.0).toFixed(0) + "%";
    },
}
export default commonService


