import Vue from 'vue'
import Vuex from 'vuex'
import 'js-base64'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    information_nav: undefined,
    api_token: window.config1.token,
    default_token: '1dcc40c0-b223-4e45-9c04-514b59084870',
    restful_token: undefined,
    access_token: undefined,
    admin_default: undefined,
  },
  mutations: {
    setInformation(state, navActive) {
      state.information_nav = navActive
    },
    setToken(state, token) {
      const _token = state.default_token + '|' + token;
      state.access_token = token;
      state.restful_token = Base64.encode(_token);
    },
    setAdmin(state, admin) {
      state.admin_default = admin;
    }
  },
  actions: {},
  modules: {}
})


