<template>
  <div>
    <com-tabButton @buttonClick="buttonClick" :buttonArr="buttonArr" class="tab-button"></com-tabButton>

    <van-pull-refresh v-if="buttonIndex === 0" v-model="pedding.refreshing" @refresh="onRefresh1">
      <van-list
        v-model="pedding.loading"
        :finished="pedding.finished"
        finished-text
        @load="penddingLoad"
      >
        <com-txtList :listArr="pedding.list"></com-txtList>
      </van-list>
    </van-pull-refresh>

    <van-pull-refresh v-if="buttonIndex === 1" v-model="done.refreshing" @refresh="onRefresh2">
      <van-list v-model="done.loading" :finished="done.finished" finished-text @load="doneLoad">
        <com-txtList :listArr="done.list"></com-txtList>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
export default {
  name: "listPage",
  data() {
    return {
      buttonArr: [{ name: "待办" }, { name: "已办" }],
      pedding: {
        loading: false,
        finished: false,
        refreshing: false,
        page: 1,
        list: []
      },
      done: {
        loading: false,
        finished: false,
        refreshing: false,
        page: 1,
        list: []
      },

      buttonIndex: 0,
      index: 0
    };
  },
  mounted() {
    // json假数据
    // this.$json.get("/mockjsdata/33/hw/todo/v1.0/getTodo").then(data => {
    //   console.log(data);
    // })
  },
  methods: {
    onRefresh1() {
      // 清空列表数据
      this.pedding = {
        loading: false,
        finished: false,
        refreshing: false,
        page: 1,
        list: []
      };
      this.penddingLoad();
    },
    onRefresh2() {
      // 清空列表数据
      this.done = {
        loading: false,
        finished: false,
        refreshing: false,
        page: 1,
        list: []
      };
      this.doneLoad();
    },
    listClick(id) {},
    buttonClick(name, i) {
      this.pedding = {
        loading: false,
        finished: false,
        refreshing: false,
        page: 1,
        list: []
      };
      this.done = {
        loading: false,
        finished: false,
        refreshing: false,
        page: 1,
        list: []
      };
      this.buttonIndex = i;
      if (i === 0) {
        this.penddingLoad();
      } else if (i === 1) {
        this.doneLoad();
      }
    },
    penddingLoad() {
      this.getPenddingData();
    },
    doneLoad() {
      this.getDoneData();
    },
    getPenddingData() {
      this.pedding.loading = true;
      this.$axios
        .post(
          `/api/ProcessAll/wfoperate/v1.0/pendings`,
          {
            current: this.pedding.page,
            size: 5,
            sort: {},
            condition: {}
          },
          {
            headers: {
              Authorization: "Bearer " + this.$store.state.api_token,
              "Content-Type": "application/json"
            }
          }
        )
        .then(result => {
          this.pedding.loading = false;
          this.pedding.list.push(...result.data.data.records);
          this.pedding.page++;
          if (this.pedding.list.length === result.data.data.total) {
            this.pedding.finished = true;
          }
        });
    },
    getDoneData() {
      this.done.loading = true;
      this.$axios
        .post(
          `/api/ProcessAll/wfoperate/v1.0/done`,
          {
            current: this.done.page,
            size: 5,
            sort: {},
            condition: {}
          },
          {
            headers: {
              Authorization: "Bearer " + this.$store.state.api_token,
              "Content-Type": "application/json"
            }
          }
        )
        .then(result => {
          this.done.loading = false;
          this.done.list.push(...result.data.data.records);
          this.done.page++;
          if (this.done.list.length === result.data.data.total) {
            this.done.finished = true;
          }
        });
    }
  }
};
</script>

<style lang="less" scoped>
.tab-button {
  margin-top: 2vh;
  .button-item {
    // width: 40px;
    text-align: center;
  }
}
</style>



