<template>
  <div>
    <div class="all-box">
      <!-- <div class="label">会议内容：</div> -->
      <van-field
        v-model="message"
        rows="2"
        autosize
        label="活动内容："
        type="textarea"
        maxlength="50"
        placeholder="添加活动内容"
        show-word-limit
      />
    </div>
    <van-cell
      :title="obj.calendarTime + ' '+obj.meetingTime"
      icon="clock-o"
      is-link
      @click="pickerShow = true"
    >
      <template #icon>
        <img src="../../../../assets/image/calendarTime.png" alt />
      </template>
    </van-cell>

    <van-cell :title="obj.jobLocation" icon="shop-o">
      <template #icon>
        <img src="../../../../assets/image/jobLocation.png" alt />
      </template>
      <template #title>
        <van-field v-model="obj.jobLocation" :readonly="auth" />
      </template>
    </van-cell>
    <van-cell title="参会人" icon="shop-o" :value="obj.correlationPerson" is-link>
      <template #icon>
        <img src="../../../../assets/image/correlationPerson.png" alt />
      </template>
    </van-cell>
    <van-popup v-model="pickerShow" position="bottom" :style="{ height: '50%' }">
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        title="选择时间"
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="pickerShow = false"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "workPage",
  data() {
    return {
      auth: true,
      show: false,
      pickerShow: false,
      message: "",
      minDate: new Date(),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      obj: {},
      compere: ""
    };
  },
  mounted() {
    this.obj = {
      calendarTime: "选择活动时间",
      meetingTime: "",
      jobLocation: "填写会议地点"
    };
  },
  methods: {}
};
</script>

<style lang="less" scoped>
.all-box {
  padding: 0;
}
.label {
  font-size: 16px;
}
.van-cell {
  margin: 2vh 0;
  font-size: 16px;
  img {
    height: 24px;
    margin-right: 4%;
  }
  .van-cell__value {
    color: #000;
  }
  // 疑问 用input的话怎么取消禁用
  // input[type="text"] {
  //   border: none;
  // }
  .van-cell.van-field {
    margin: 0 !important;
    padding: 0 !important;
  }
}
</style>


