<template>
  <div class="tab-button">
    <div
      :class="buttonModel === index ? 'button-item button-item-active' : 'button-item'"
      v-for="(item, index) in buttonArr"
      :key="item.name"
      @click="buttonclick(index)"
    >{{item.name}}</div>
  </div>
</template>

<script>
export default {
  name: "com-tabButton",
  props: {
    buttonArr: {
      default: []
    }
  },
  data() {
    return {
      buttonModel: 0
    };
  },
  mounted() {
    this.buttonclick(this.buttonModel);
  },
  methods: {
    buttonclick(i) {
      this.buttonModel = i;
      this.$emit("buttonClick", name, i);
    }
  }
};
</script>

<style lang="less" >
.tab-button {
  display: flex;
  justify-content: space-around;
  margin-bottom: 1vh !important;
  .button-item {
    min-width: 46px !important;
    text-align: center;
    padding: 1vh 4.5vw;
    background-color: #ffffff;
    border: 1px solid #0088fe;
    color: #0088fe;
    border-radius: 4px;
    font-size: 15px;
  }
  .button-item-active {
    padding: 1vh 4.5vw;
    background-color: #0088fe;
    color: #fff;
    border-radius: 4px;
  }
}
</style>



