import axios from 'axios'

const environment = {
  apiurl: 'http://36.112.138.131:8088',
  // apiurl: 'http://36.112.135.178:8100',
  // mockurl: './',
  mockurl: '/static/json/'
};


const Http = {
  get(endpoint) {
    this.url = endpoint.indexOf('.json') !== -1 ? environment.mockurl : environment.apiurl;
    return axios.get(this.url + endpoint);
  },
}

export default Http




