<template>
  <div>
    <van-tabs v-model="active">
      <van-tab title="待办">
        <template #title>
          <span>待办</span>
          <div class="title-circle">
            <div class="circle-num">5</div>
          </div>
        </template>
        <wait-list></wait-list>
      </van-tab>
      <van-tab title="消息">
        <template #title>
          <span>提醒</span>
          <div class="title-circle">
            <div class="circle-num">3</div>
          </div>
        </template>
        <remind-list></remind-list>
      </van-tab>
      <van-tab title="日历">
        <calendar-list></calendar-list>
      </van-tab>
      <van-tab title="日历demo">
        <demo />
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import waitList from "@/views/information/wait-do/list.vue";
import remindList from "@/views/information/remind/index-list.vue";
import calendarList from "@/views/information/calendar/index-list.vue";
import demo from './demo'
export default {
  components: {
    remindList,
    waitList,
    calendarList,
    demo
  },
  data() {
    return {
      active: 0
    };
  },
  mounted() {
    if (this.$store.state.information_nav) {
      this.active = this.$store.state.information_nav;
    } else {
      this.active = 0;
    }
  }
};
</script>

<style  scoped lang="less">
.title-circle {
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: #fc602d;
  position: absolute;
  right: 20%;
  top: -4px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  .circle-num {
    color: #ffffff;
    font-size: 13px;
    // letter-spacing: -2px;
  }
}
</style>


