<template>
  <div class="icon-list-box">
    <div class="icon-list" v-for="(item,index) in iconListArr" :key="index">
      <div class="icon-box">
        <div class="icon" :style="listBackColor[index]"></div>
        <span class="name" :title="item.name">{{item.name.substring(0,4)}}</span>
      </div>
      <div class="middle">
        <span class="percent">{{item.midValue}}</span>
        <span class="unit" v-if="midUnitShow">个</span>
      </div>
      <div class="right">
        <span class="value">{{item.value}}</span>
        <span class="unit">亿美元</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "com-iconList",
  props: {
    iconListArr: {
      default: []
    },
    midUnitShow: {
      default: false
    }
  },
  data() {
    return {
      // listBackColor: [
      //   "background-color:#0088fe",
      //   "background-color:#00a7fe",
      //   "background-color:#11c05f",
      //   "background-color:#fc602d",
      //   "background-color:#f58a22",
      //   "background-color:#f5c622",
      //   "background-color:#d066f3"
      // ]
      listBackColor: [
        "background-color:#0088fe",
        "background-color:#f5c622",
        "background-color:#fc602d",
        "background-color:#f58a22",
        "background-color:#d066f3",
        "background-color:#11c05f",
        "background-color:#00a7fe"
      ]
    };
  },
  mounted() {}
};
</script>

<style lang="less" >
</style>



