// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
// import ECharts from "vue-echarts/components/ECharts.vue";
import Http from './api/http'
import './api/axios'
import _ from 'lodash'
import ECharts from "vue-echarts";
import theme from "./assets/theme/echart-theme.json";
import ChartInitService from '../static/echarts/chart.init'
import ChartHandleService from '../static/echarts/charts.handel'
import world from '../static/echarts/world.json'
import 'vant/lib/index.css';
import 'vant/lib/index.less';
// import 'vant/lib/index.less';
import { Tabbar, TabbarItem } from "vant";
import { Tab, Tabs } from 'vant';
import { NavBar } from 'vant';
import { Calendar } from 'vant';
import { Cell, CellGroup } from 'vant';
import { List } from 'vant';
import { DatetimePicker } from 'vant';
import { Field } from 'vant';
import { Icon } from 'vant';
import { Popup } from 'vant';

import ZcttCalendar from './components/zctt-calendar/index'


import tabButton from "./components/com-tabButton.vue";
import kip from "./components/com-kip.vue";
import iconList from "./components/com-iconList.vue";
import txtList from "./components/com-txtList.vue";
import table from "./components/com-table.vue";
import { PullRefresh } from 'vant';

Vue.config.productionTip = false;
Vue.prototype.$json = Http;
Vue.prototype.$_ = _
Vue.prototype.ChartInitService = ChartInitService
Vue.prototype.ChartHandleService = ChartHandleService


ECharts.registerTheme('echart-theme', theme);
ECharts.registerMap('world', world);

Vue.use(Tabbar).use(TabbarItem);
Vue.use(Tab).use(Tabs);
Vue.use(NavBar);
Vue.use(Calendar);
Vue.use(Cell).use(CellGroup);
Vue.use(PullRefresh);
Vue.use(List);
Vue.use(DatetimePicker);
Vue.use(Field);
Vue.use(Icon);
Vue.use(Popup);
Vue.use(ZcttCalendar);

Vue.component('app-echarts', ECharts)
Vue.component('com-tabButton', tabButton)
Vue.component('com-kip', kip)
Vue.component('com-iconList', iconList)
Vue.component('com-txtList', txtList)
Vue.component('com-table', table)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  template: '<App/>'
})



