import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld2 from '@/views/HelloWorld'
import Information from '@/views/information/index'
import Analysis from '@/views/analysis/index'
import Work from '@/views/work/index'
import My from '@/views/my/index'
import addMeeting from "@/views/information/calendar/add-meeting/index.vue";
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/homepage2',
      name: 'HelloWorld',
      component: HelloWorld2
    },
    {
      path: '',
      redirect: '/information'
    },
    {
      path: '/information',
      name: 'Information',
      component: Information
    },
    {
      path: '/analysis',
      name: 'Analysis',
      component: Analysis
    },
    {
      path: '/work',
      name: 'Work',
      component: Work
    },
    {
      path: '/my',
      name: 'My',
      component: My
    },
    {
      path: '/information/addmeeting',
      name: 'Addmeeting',
      component: addMeeting
    },
  ]
})



